.list_wrapper ul {
  padding-left: 0;
}

.list_wrapper ul li {
  width: 250px;
  list-style: none;
  position: relative;
}

.list_wrapper ul li img {
  border: 1px solid #000;
  width: 100%;
  padding: 15px;
  border-radius: 25px;
}

.list_wrapper ul li .fname {
  display: block;
  text-align: center;
  margin-top: 5px;
}

.emp_id {
  position: absolute;
  right: -25px; /* Adjusted the value to better center the emp_id */
  top: -10px;
  background: #000;
  color: #fff;
  padding: 5px;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px; /* Adjusted the font size to better fit the circle */
}

.search_input {
  padding: 0.5rem 1rem;
  border: 1px solid #f3f4f6;
  border-radius: 5px;
}

.custom_container {
  max-width: 90%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
